.posts{
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 0;
    margin: 0;
}

.posts__list-item{
    flex-basis: 48%;

}

.posts__img{
    display: block;
    width: 100%;
}

.posts__title{
    font-size: 24px;
    line-height: 1;
    font-weight: normal;
    margin: 0 0 50px;
}

.posts__text{
    font-size: 14px;
    line-height: 1.286;
}

.posts__bottom-line{
    display: block;
    width: 100px;
    height: 2px;
    margin-top: 25px;
    margin-left: auto;
    background-color: red;
}

@media (max-width:980px) {

    .news__title {//
      margin-bottom: 40%;
      font-size: 30px;
    }
  
  }
  
  @media (max-width: 560px) {
  
    .posts{
      flex-direction: column;
    }

    .posts__list-item{
        margin-top: 25px;
    }
}