.socials{
    list-style: none;
    padding: 0;
    display: flex;
    align-items:flex-end;
}

.socials__element{
    margin: 0 6px;

    &:last-child{
        margin-right: 0;
    }
}

.socials__facebook {
    width: 10px;
    height: 22px;
    fill: $second;
  }
  
  .socials__twitter {
    width: 26px;
    height: 21px;
    fill: $second;
  }
  
  .socials__linkedin {
    width: 24px;
    height: 23px;
    fill: $second;
  }
  
  .socials__google {
    width: 19px;
    height: 19px;
    fill: $second;
  }