.footer{
    display: grid;
    grid-template-columns: repeat(16, 1fr);
    background-color: #ebebeb;
}

.footer__left{
    grid-column: 1/9;
    grid-row: 1/2;
}

.footer__right{
    grid-column: 9/17;
    grid-row: 1/2;
}

.footer__right{
    padding: 15% 23% 6% 18%;
    font-family: 'Roboto', sans-serif;
}

.footer__right-box{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.footer__contacts{
    padding-bottom: 59%;
    flex-basis: 55%;
}

.footer__address{
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 35px;

}

.footer__tel, .footer__mail{
    text-transform: uppercase;
    text-decoration: none;
    font-size: 18px;
    color: $black;
}


.footer__logo{
    flex-basis: 30%;
}

.footer__logo-img{
    display: block;
    width: 100%;
}

@media (max-width: 980px){
    .footer__right{
        padding: 5%;
    }

    .footer__right-box{
        align-items: start;
    }

    .footer__logo{
        flex-basis: 20%;
    }

    .footer__address{
        font-size: 14px;
    }
}

@media (max-width: 768px){

    .footer{
        grid-template-columns: 1fr;
    }

    .footer__right{
        padding-left: 10%;
        grid-column: span 1;
    }

    .footer__left{
        grid-column: span 1;
        grid-row: span 1;
        height: 50%;
    }

    .footer__contacts{
        padding-bottom: 10px;
    }

}