.like{
    display: flex;
    align-items: center;
}

.like__btn{
    background: none;
    border: none;
    box-shadow: none;
    cursor: pointer;
    padding: 0;

    svg{
        width: 30px;
        height: 30px;
        transition-property: fill;
        transition-duration: 500ms;
    }

    &:hover{

        svg{
            fill: $second;
        }
    }
}

.like__counts{
    font-size: 11px;
    color: $second;
    text-transform: uppercase;
    line-height: 1.091;
    display: block;
    margin: 0 0 0 5px;

    @media (min-width: 1500px) {
        @include dynamicText(68.75%, 22, 11, 1500px, 3000, 1500);
    }
}