.persons{
    display: flex;
    justify-content: space-between; 
    list-style: none;
    padding: 0;
    margin: 0;
}

.persons__item {
    flex-basis: 32%;
}
  
  .persons__img {
    display: block;
    width: 100%;
}

.persons__name {
    font-size: 13px;
    text-transform: uppercase;
    line-height: 1.231;
    margin: 5.5% 0 0 0;
}
  
.persons__post {
    font-size: 12px;
    text-transform: uppercase;
    line-height: 1.333;
    margin: 0 0 5.5% 0;
}

@media (max-width: 980px) {

    .persons {
      flex-direction: column;
    }
  
    .persons__item {
      flex-basis: 47%;
    }

    .persons__name{
      margin-top: 10px;
      margin-bottom: 50px;
    }
  
}
  
@media (max-width: 560px) {
  
    .persons {
      flex-direction: column;
      flex-wrap: nowrap;
    }
  
    .persons__item {
      flex-basis: auto;
    }
  
}