$bgBlog: #545e75;

.blog{
    display: grid;
    grid-template-columns: repeat(16, 1fr);
}

.blog__left{
    grid-column: span 5;
    background-color: $bgBlog;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
    }
}

.blog__title{
    font-size: 48px;
    color: $white;
    text-transform: uppercase;
    line-height: 1;
    font-weight: normal;
    margin: 64% 0 32% 53%;

    @media (min-width: 1500px) {
        @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
    }
}

.blog__right{
    grid-column: span 11;
    padding: 8% 17% 6% 10%;
}

@media (max-width: 980px) {

    .blog__left {
      grid-column: span 16;
    }
  
    .blog__right {
      grid-column: span 16;
      display: block;
      padding: 5%;
    }

    .blog__title{
        margin: 10% 0 30% 10%;
    }
}