$bgStudio: #3f826d;

.studio{
    display: grid;
    grid-template-columns: repeat(16, 1fr);
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.studio__left, .studio__staff{
    grid-column: span 5;
}

.studio__right, .studio__persons {
    grid-column: span 11;
}

.studio__left{
    background-color: $bgStudio;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
    }
}

.studio__title{
    font-size: 48px;
    color: $white;
    text-transform: uppercase;
    line-height: 1;
    font-weight: normal;
    margin: 35% 0 10% 10%;

    @media (min-width: 1500px) {
        @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
    }
}

.studio__right{
    display: flex;
    padding: 10% 17% 3% 12%;
}

.studio__information {
    flex-basis: 48%;
    margin-right: 4%;
}
  
.studio__description {
    flex-basis: 48%;
}

.studio__img {
    display: block;
    max-width: 100%;
}

.studio__text {
    font-size: 16px;
    line-height: 1.2;
    margin: 5.4% 0;
  
    &:first-child {
      margin-top: 0;
    }

    @media (min-width: 1500px) {
        @include dynamicText(112.5%, 32, 16, 1500px, 3000, 1500);
    }  
}

.studio__text--bold-italic {
    font-weight: 400;
}

.studio__subtitle {
    font-size: 14px;
    color: $colorText;
    text-transform: uppercase;
    line-height: 1.286;
    margin: 9.4% 0 5%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.18);

    @media (min-width: 1500px) {
        @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
    }
}

.studio__list {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: space-between;
    margin: 2.5% 0;
}

.studio__List-item {
    font-size: 12px;
    text-transform: uppercase;
    line-height: 1.167;
    flex-basis: 45%;

    @media (min-width: 1500px) {
        @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
    }  
}

.studio__staff {
    padding: 10%;
    font-family: 'Roboto', sans-serif;
}
  
.studio__staff-text {
    font-size: 14px;
    line-height: 1.2;
    margin: 7.4% 0;

    @media (min-width: 1500px) {
        @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
    }

    &:last-child {
        margin-bottom: 0;
    }

}

.studio__staff-title {
    font-size: 14px;
    text-transform: uppercase;
    line-height: 1;
    margin: 0 0 7.4% 0;

    @media (min-width: 1500px) {
        @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
    }
}

.studio__persons {
    padding-top: 6%;
    padding-right: 17%;
}

@media (max-width: 980px){

    .studio__title {
        margin: 10% 0 30% 10%;
    } 

    .studio__left{
        grid-column: span 16;
    }

    .studio__right {
        grid-column: span 16;
        padding: 10%;
    }

    .studio__staff{
        grid-column: span 9;
    }

    .studio__persons{
        grid-column: span 7;
        padding: 5%;
    }
}

@media (max-width: 560px) {

    .studio {
      grid-template-columns: 1fr;
    }
  
    .studio__left, .studio__right, .studio__staff, .studio__persons {
      grid-column: span 1;
    }

    .studio__right {
      display: block;
    }
}