.projects{
    display: grid;
    grid-template-columns: repeat(16, 1fr);
}

.projects__element--one{
    background-color: $thirty;
    grid-column: span 5;
    grid-row: span 2;
    position: relative;

    &::before{
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url('data:image/svg+xml; utf-8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
    }
}

.projects__title{
    font-size: 48px;
    color: $white;
    text-transform: uppercase;
    line-height: 1;
    font-weight: normal;
    margin: 87% 0 33% 34%;

    @media (min-width: 1500px) {
        @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
    }
}

.projects__element--two{
    background-image: url('/img/image2.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 6;
    grid-row: span 2;
    display: flex;
    align-items: flex-end;

    .projects__info{
        max-width: 45%;
        margin-left: 7%;
        margin-top: 62%;
        margin-bottom: 11%;
    }

    .projects__like{
        margin-top: 73%;
        margin-right: 8%;
        margin-bottom: 11%;
        margin-left: auto;
    }
}

.projects__element--three{
    background-image: url('/img/image3.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 5;
    grid-row: span 1;
    display: flex;
    align-items: flex-end;

    .projects__info{
        max-width: 45%;
        margin-left: 6%;
        margin-top: 22%;
        margin-bottom: 10%;
        color: $white;
    }

    .projects__like{
        margin-right: 8%;
        margin-bottom: 5%;
        margin-left: auto;
    }
}

.projects__element--fore{
    background-image: url('/img/image4.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 5;
    grid-row: span 2;
    display: flex;
    align-items: flex-end;

    .projects__info{
        max-width: 46%;
        margin-left: 11%;
        margin-top: 22%;
        margin-bottom: 8%;
        color: $black;
    }

    .projects__like{
        margin-right: 12%;
        margin-bottom: 9%;
        margin-left: auto;
    }
}

.projects__element--five{
    background-image: url('/img/image5.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 4;
    grid-row: span 2;    
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .projects__info{
        max-width: 82%;
        margin-left: auto;
        margin-right: 7%;
        margin-top: 95%;
        margin-bottom: 55px;
        color: $white;
        text-align: right;
    }

    .projects__like{
        margin-right: 7%;
        margin-bottom: 5%;
        margin-left: auto;
    }
}


.projects__element--six{
    background-image: url('/img/image6.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 7;
    grid-row: span 2;
    display: flex;
    align-items: flex-start;
    flex-direction: column;

    .projects__info{
        max-width: 40%;
        margin-left: 4%;
        margin-right: 7%;
        margin-top: 8%;
        margin-bottom: 29%;
        color: black;
    }

    .projects__like{
        margin-right: 7%;
        margin-bottom: 8%;
        margin-top: auto;
        margin-left: auto;
    }
}

.projects__element--seven{
    background-image: url('/img/image7.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 5;
    grid-row: span 1;
    display: flex;
    align-items: flex-start;

    .projects__info{
        max-width: 53%;
        margin-left: auto;
        margin-right: 11%;
        margin-top: 11%;
        margin-bottom: 32%;
        color: black;
    }

    .projects__like{
        order: -1;
        margin-top: 8%;
        margin-left: 8%;
    }
}

.projects__element--eight{
    background-image: url('/img/image8.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 8;
    grid-row: span 1;
    display: flex;
    align-items: flex-end;

    .projects__info{
        max-width: 45%;
        margin-left: 10%;
        margin-top: 29%;
        margin-bottom: 3%;
        color: black;
    }

    .projects__like{
        margin-bottom: 3%;
        margin-left: auto;
        margin-right: 7%;
    }
}

.projects__element--nine{
    background-image: url('/img/image9.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    grid-column: span 4;
    grid-row: span 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .projects__info{
        max-width: 47%;
        margin-left: 11%;
        margin-top: 35%;
        margin-bottom: 10%;
        color: $white;
    }

    .projects__like{
        order: -1;
        margin-bottom: auto;
        margin-left: 15%;
        margin-top: 8%;
    }
}

.projects__element--ten{
    background-color: $thirty;
    grid-column: span 4;
    grid-row: span 1;
    position: relative;

    &::before{
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url('data:image/svg+xml; utf-8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
    }
}

.like__btn{
    &:focus, &:active{
        outline: none;
    }
}

.projects__name{
    font-size: 24px;
    //font-family: ;
    //color: $black;
    text-transform: uppercase;
    line-height: 1;
    font-weight: normal;
    margin: 0 0 13px 0;

    @media (min-width: 1500px) {
        @include dynamicText(150%, 48, 24, 1500px, 3000, 1500);
    }
}

.projects__description{
    font-size: 12px;
    text-transform: uppercase;
    line-height: 1.3;
    margin: 0;

    @media (min-width: 1500px) {
        @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
    }
}

@media (max-width: 980px) {

    .projects__element--one {
      grid-column: span 7;
    }

    .projects__title{
        font-size: 48px;
        color: $white;
        margin: 10% 0 35% 10%;
    
        @media (min-width: 1500px) {
            @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
        }
    }
  
    .projects__element--two {
      grid-column: span 9;
    }
  
    .projects__element--three {
      grid-column: span 9;
      grid-row: span 2;
    }
  
    .projects__element--fore {
      grid-column: span 7;
      grid-row: span 2;
    }
  
    .projects__element--five {
      grid-column: span 6;
    }
  
    .projects__element--six {
      grid-column: span 10;
    }
  
    .projects__element--seven {
      grid-column: span 8;
    }
  
    .projects__element--eight {
      grid-column: span 8;
    }
  
    .projects__element--nine {
      grid-column: span 9;
    }
  
    .projects__element--ten {
      grid-column: span 7;
    }
  
}

  @media (max-width: 560px) {

    .projects {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr;
    }
  
    .projects__element--one, .projects__element--two, .projects__element--three, .projects__element--fore, .projects__element--five, .projects__element--six, .projects__element--seven, .projects__element--eight, .projects__element--nine, .projects__element--ten {
      grid-column: span 1;
      grid-row: span 1;
    }

    .projects__element--ten{
        height: 200px;
    }
}